import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import IntlTelInput from 'react-intl-tel-input';
import 'react-intl-tel-input/dist/main.css';
import { getIPDetails, otherInfoSkip, otherInfoUpate } from '../../../actions/loginActions' 

const isValidNumber = (n) => {
    return /^-?[\d.]+(?:e-?\d+)?$/.test(n);
}

class OtherInfo extends Component {
    constructor(props) { super(props) }

    onInTelLoad = () => {

        var errorMsg = document.querySelector("#error-msg"),
            validMsg = document.querySelector("#valid-msg");
        var errorMap = ["Invalid number", "Invalid country code", "Too short", "Too long", "Invalid number"];
        var submitBtn = document.getElementById('register-submit-btn');
        var contactError = document.getElementById('contact-error');

        var input = document.querySelector("#phone");

        if (input) {
            var reset = function () {
                input.classList.remove("error");
                errorMsg.innerHTML = "";
                errorMsg.classList.add("hide");
                submitBtn.disabled = false;

            };
            input.addEventListener('blur', function () {
                reset();
                var contactNo = input.value.trim();

                if (contactNo) {

                    if (isNumber(contactNo)) {
                        validMsg.classList.remove("hide");
                        submitBtn.disabled = false;
                        contactError.style.display = "none";

                    } else {
                        input.classList.add("error"); 
                        errorMsg.classList.remove("hide");
                        submitBtn.disabled = true;
                        contactError.style.display = "none";

                    }

                }
            });
            input.addEventListener('change', reset);
            input.addEventListener('keyup', reset);
            function isNumber(n) {
                return /^-?[\d.]+(?:e-?\d+)?$/.test(n);
            }
        }
    }
    state = { accountCompanyName: null, acnError: null, phoneError: null, phone: null, country_code: null, email: null, emailError:false, countrySortname: null }
    onACNChange = event => this.setState({ accountCompanyName: event.target.value, acnError: null })
    onClickToSkip = event => {
        const { history } = this.props
        this.props.otherInfoSkip(history)
    }
    onClickToFinish = event => {
        const { history, signUpData,addEmail } = this.props
        const { accountCompanyName, phone, country_code, email } = this.state;
        const payload = {  organization: accountCompanyName, contact_no: phone, country_code: country_code }
        if(addEmail){
            if(!email || email == '') {this.setState({emailError: true}); return}
            else payload['email'] = email
        }
        if (accountCompanyName && String(accountCompanyName).length > 0 && phone && country_code) {
            this.props.otherInfoUpate( payload, history)
        }
        else if(email){
            this.props.otherInfoUpate( {organization: "", contact_no: "", country_code: "", email: email}, history)
        }
        else { this.props.otherInfoSkip(history) }
    }
    componentDidMount() {
        this.onInTelLoad();
        setTimeout(() => {
            this.getIpDetails();
        }, 100);
    }
    getIpDetails = async () => {
        try {
          let response = await getIPDetails();
          if (response && response.data && response.data.country) {
            this.setState({ countrySortname: response.data.country });
          } else {
            this.setState({ countrySortname: 'US' });
          }
    
        } catch (e) {
          this.setState({ countrySortname: 'US' });
        }
      }

    updatePhoneNumber1 = (isValid, value, selectedCountryData, fullNumber, extension) => {


        var errorMsg = document.querySelector("#error-msg"),
            validMsg = document.querySelector("#valid-msg");
        var submitBtn = document.getElementById('register-submit-btn');
        var contactError = document.getElementById('contact-error');
        var input = document.querySelector("#phone");
        if (value) {
            if (isValid && isValidNumber(value)) {
                validMsg.classList.remove("hide");
                validMsg.innerHTML = " Valid";
                submitBtn.disabled = false;
                contactError.style.display = "none";
                this.setState({ phone: value, country_code: selectedCountryData.dialCode })

            } else {
                input.classList.add("error"); 
                errorMsg.innerHTML = "Invalid number";
                errorMsg.classList.remove("hide");
                validMsg.innerHTML = "";
                validMsg.classList.add("hide");
                submitBtn.disabled = true;
                contactError.style.display = "none";
            }
        }
    }

    render() {
        const { prefix, addEmail } = this.props
        const { accountCompanyName, acnError, email, emailError, countrySortname } = this.state;

        return <>
        {countrySortname ? (
            <div className="loginFormSec registerFormSec formControlMin">
                <div className="loginWithSocials">
                    <div className="loginHead">
                        <h2>You're nearly there </h2>
                    </div>
                </div>
                <div className="loginBlock">
                    <div className="loginInnSec">
                        <div id="AccountOtherinfoForm">
                            <div className="row">
                                <div className="col-sm-12">
                                    {addEmail ? <div className="form-group labelPosition">
                                        <label htmlFor="AccountCompanyName">Email</label>
                                        <input name="data[Account][company_name]" className={`form-control placeholder-no-fix ${acnError ? ` form-error` : ``}`} type="email" value={email} id="AccountEmail" data-factors-input-id="form-0.field-0" onChange={(e) => this.setState({email: e.target.value, emailError: false})} />
                                        {emailError ? <span className="errorMessage">Email is required.</span> : null}
                                    </div> : <></>}
                                    <div className="form-group labelPosition">
                                        <label htmlFor="AccountCompanyName">Company Name</label>
                                        <input name="data[Account][company_name]" className={`form-control placeholder-no-fix ${acnError ? ` form-error` : ``}`} type="text" value={accountCompanyName} id="AccountCompanyName" data-factors-input-id="form-0.field-0" onChange={this.onACNChange} />
                                        {acnError ? <span className="errorMessage">Company Name is required.</span> : null}
                                    </div>
                                    <div className="form-group labelPosition">
                                        <input type="hidden" name="data[Account][country_code]" id="country_code" className="form-control placeholder-no-fix" val="1" />
                                        <label htmlFor="phone">Contact No</label>
                                        <IntlTelInput fieldId="phone" style={{ width: `100%` }} preferredCountries={[countrySortname]} onPhoneNumberBlur={this.updatePhoneNumber1} containerClassName="intl-tel-input" inputClassName="form-control placeholder-no-fix" autoComplete="off" data-intl-tel-input-id="0" placeholder="9987654321" data-factors-input-id="form-0.field-1" />

                                        <div id="contact-error"> </div>
                                        <div id="error-msg" className="hide" style={{ left: 0, color: '#d41220', font: "400 12px/25px 'open sans', sans-serif" }}></div>
                                        <div id="valid-msg" className="hide" style={{ left: 0, color: 'green', font: "400 12px/25px 'open sans', sans-serif" }}></div>

                                    </div>
                                </div>
                                <div className="col-sm-12">
                                    <div style={{ clear: 'both' }}></div>
                                    <div className="form-group">
                                        <button onClick={this.onClickToFinish} id="register-submit-btn" data-style="expand-left" className="btn btn-primary w-100"> Finish </button>
                                    </div>
                                    {!addEmail ? <div className="skip_btn_wrap">
                                        <p> <a className="skip_btn" onClick={this.onClickToSkip} style={{ cursor: 'pointer' }}>Skip</a></p>
                                    </div> : <></>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="clear"></div>
            </div>
        ) : null}
        </>
    }
}
const mapStateToProps = (state) => {
    return {
        signUpData: state.signUp.signUpData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        otherInfoSkip: (history) => dispatch(otherInfoSkip(history)),
        otherInfoUpate: (data, history, redirect) => dispatch(otherInfoUpate(data, history, redirect)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(OtherInfo);